.note {
  min-height: 30px;
  padding: 10px;
  margin: 0;
  line-height: 30px;
}

.note:nth-child(1) {
  margin-top: -1px;
}

.note:hover {
  background-color: rgb(255, 99, 71);
  color: white;
  cursor: pointer;
}

.note-selected {
  background-color: rgb(255, 99, 71);
  color: white;
}



